import * as TSHash from 'js-sha512';
// var XMLHttpRequest = require("xmlhttprequest").XMLHttpRequest;
// var hash = require()

export module PayU {
    export class PayUResponse {
        public transactionId : string;
        public statusCode : string;
        public statusDescription : string;
        public url : string;
        public email : string;

        public errorCode : string;
        public errorDescription : string;

        public debugRequest : string;
        public debugResponse : string;

        constructor() {
            this.errorCode = "";
            this.statusCode = "";
        }
    }

    export function payuParseResp(response: string) : any
    {
        const preRegex = new RegExp('^<pre>(.*?)<\/pre>$', 'ims');
        var match = preRegex.exec(response);
        let resp = {};
        
        if (match && match.length == 2) {
            let text = match[1];
    
            if (!text.startsWith('Array')) {
                resp['ErrorCode'] = '101';
                resp['Error'] = text;
            } else {
                resp['ErrorCode'] = '0';
                text.split(/\n/g).forEach(function (line) {
                    let mmm = (/\[(.*?)\]\s+=>\s+(.*?)$/).exec(line)
                    if (mmm && mmm.length == 3) {
                        resp[mmm[1]] = mmm[2];
                    }
                });
            }
        } else {
            resp['ErrorCode'] = '100';
            resp['Error'] = 'unknown answer';
        }
    
        return resp;
    }

    export class PayUWrapper {
        private _endpoint : string;
        private _merchantId : string;
        private _salt : string;
    
        constructor(baseUrl: string, merchantId: string, salt: string)
        {
            this._endpoint = baseUrl;
            this._merchantId = merchantId;
            this._salt = salt;
        }

        _encodeQueryData(data: { [key: string]: string }) {
            const ret = [];
            for (let key in data) {
                let value = data[key];
                ret.push(encodeURIComponent(key) + '=' + encodeURIComponent(value));
            }
            return ret.join('&');
        }
        
        createInvoice(request : any) : Promise<PayUResponse> 
        {
            var jsonRequest = JSON.stringify(request);
            let hashString = `${this._merchantId}|create_invoice|${jsonRequest}|${this._salt}`;
            // let hashRes = hash.sha512(hashString);
            let hashRes = '';
            
            console.log('Message hash');
            console.log(hashString);
            console.log(hashRes);
            
            var data: { [key: string]: string } = {
                "key": this._merchantId,
                "command": "create_invoice",
                "hash": hashRes,
                "var1": jsonRequest
            };

            let requestBody = this._encodeQueryData(data);

            return new Promise<PayUResponse>((resolve, reject) => {
                var rr = new PayUResponse();
                rr.transactionId = request.txnid;
                rr.debugRequest = requestBody;
                rr.errorCode = "0";

                var xhr = new XMLHttpRequest();
                xhr.onreadystatechange = function() {
                    try {
                        if (xhr.readyState !== 4) return;
                        if (xhr.status !== 200) throw new Error("Failed with status " + xhr.status + "; " + xhr.responseText);
        
                        rr.debugResponse = xhr.responseText;

                        // parse response
                        const resp = payuParseResp(xhr.responseText);

                        if (resp['Error']) {
                            rr.errorCode = resp['ErrorCode'];
                            rr.errorDescription = resp['Error'];
                            rr.statusCode = '100';
                        } else {
                            rr.transactionId = resp['Transaction Id'];
                            rr.url = resp['URL'];
                            rr.email = resp['Email Id'];
                        }

                        resolve(rr);
                    } catch (e) {
                        rr.errorCode = "-1";
                        rr.errorDescription = e.message;

                        reject(rr);
                    }
                };
                
                xhr.open("POST", this._endpoint);
                xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
                xhr.send(requestBody);
            });

        }
    }
}